import React, {Component} from 'react';
import Header from '../../components/Header/Header';
import Contents from '../../components/Contents/Contents';
import Footer from '../../components/Footer/Footer';
import './About.css';

class About extends Component {
  render(){
    return (
      <div>
        <Header />
        <Contents>
          <AboutContents />
        </Contents>
        <Footer />
      </div>
    );
  }
}

class AboutContents extends Component {
  render(){
    return (
      <div className="about-contents">
        <div className="about-header">
          <h2>About blog</h2>
        </div>
        <div className="about-band"></div>
        <div className="about-body">
          <div className="description">
            <p>
                   ReactJS    .<br />
                       .<br />
                       .<br />
                         ,   ,          .      ...
            </p>
          </div>
          <div className="summary">
            <div className="me">
              <h4>Me</h4>
              <ul>
                <li>Web publisher</li>
                <li>female</li>
                <li>20's</li>
              </ul>
            </div>
            <div className="skill">
              <h4>Skills</h4>
              <ul>
                <li>HTML</li>
                <li>CSS</li>
                <li>Javascript</li>
                <li>jQuery</li>
                <li>Bootstrap</li>
              </ul>
            </div>
            <div className="study">
              <h4>Studying</h4>
              <ul>
                <li>ReactJS</li>
                <li>NodeJS</li>
                <li>Javascript Libraries...</li>
                <li>Javascript Frameworks...</li>
              </ul>
            </div>
            <div className="portfolio">
              <h4>Work</h4>
              <ul>
                <li>Samsung Galaxy S series Mobile Manual...</li>
                <li>Company website...</li>
              </ul>
            </div>
            <div className="contact">
              <h4>Contact</h4>
              <ul>
                <li>qkrekwjd9715@naver.com</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default About;



