import React, { Component } from 'react';
import Header from '../../Header/Header';
import Contents from '../../Contents/Contents';
import Footer from '../../Footer/Footer';
import '../HistoryContents.css';
import './HistoryData.css';

class Data01 extends Component {
  render(){
    return (
      <div>
        <Header />
        <Contents>
          <Data />
        </Contents>
        <Footer />
      </div>
    );
  }
}

class Data extends Component {
  render(){
    return (
      <div className="history-contents">
        <div className="history-header">
          <h2>History</h2>
        </div>
        <div className="history-band"></div>
        <div className="history-body">

          { /* Data01 start */ }
          <div className="main_text">
            <h2>        </h2>
            <span className="date">2017 4 5</span>
            <p>
              create-react-app    .<br />
                 ...<br />
                     npm run build   build ,         xampp  .<br />
                       !<br />
                , .   .....
            </p>
            <img src={require('../../../images/history/history01/history01_00.jpg')} alt="   " />
            <p>
                          ???<br />
              <br />
                    .  ...  .    .<br />
              <br />
                . babel? webpack ?   .<br />
              <br />
                    <br />
              <br />
                             ,         .  <br />
              <br />
                 !<br />
                    xampp (apache )  .<br />
               .htaccess   URL     .<br />
              <br />
               build     .htaccess        .
            </p>
            <div className="code">
              <pre>
                <code>
{`RewriteEngine on
RewriteBase /

RewriteCond %{REQUEST_FILENAME} !-f
RewriteCond %{DOCUMENT_ROOT} !-f
RewriteRule ^(.+)$ /index.html [L]`}
                </code>
              </pre>
            </div>
            <p>
                .<br />
              <br />
                .    .<br />
              <br />
               ,     ,     ...<br />
              ,,     !<br />
              <br />
              10          .<br />
              <br />
              ~     
            </p>

            <div className="source">
              <h3>.htaccess      <a href="http://soul0.tistory.com/139" className="url">http://soul0.tistory.com/139</a></h3>

              <p>
                .htaccess ( " ")      .
                               .
              </p>
            </div>

            <div className="tags">
              <img src={require('../../../images/icon/tag.png')} alt=" " />
              <a href="#" rel="tag"><span>#react.js</span></a>
              <a href="#" rel="tag"><span>#</span></a>
              <a href="#" rel="tag"><span>#.htaccess</span></a>
            </div>
          </div>
          { /* Data01 end */ }

        </div>
      </div>
    );
  }
}

export default Data01;



