import React from 'react';
import { Link } from 'react-router';
import './Header.css';

const MenuItem = ({active, children, to}) => (
  <Link to={to} className={`menu-item ${active ? 'active' : ''}`}>
    {children}
  </Link>
);

const Header = (props, context) => {
  const { router } = context;
  return (
    <header id="header">
      <div className="header-wrap">
        <div className="logo"><h1><MenuItem to={'/'}>DJPARK</MenuItem></h1></div>
        <nav>
          <ul>
            <li><MenuItem to={'/'} active={router.isActive('/home', true)}>HOME</MenuItem></li>
            <li><MenuItem to={'/about'} active={router.isActive('/about')}>ABOUT</MenuItem></li>
            <li><MenuItem to={'/work'} active={router.isActive('/work')}>WORK</MenuItem></li>
            <li><MenuItem to={'/daily'} active={router.isActive('/daily')}>DAILY</MenuItem></li>
            <li><MenuItem to={'/history'} active={router.isActive('/history')}>HISTORY</MenuItem></li>
          </ul>
        </nav>
      </div>
    </header>
  );
}

Header.contextTypes = {
  router: React.PropTypes.object
}

export default Header;



/*
   

            context  router .
    contextType  .

  context React      .
   props      .

  class    this.context.router  ,
         context   .

  router   isActive          .
          IndexRoute   .
      true   false .

     .
*/



